.cart-icon {
    width: 45px;
    height: 45px;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
  
    .shopping-icon {
      width: 24px;
      height: 24px;
    }
  
    .item-count {
      position: absolute;
      font-size: 10px;
      font-weight: 600;
      bottom: 12px;
    }
    &:hover{
      .shopping-icon{
        width: 35px;
      height: 35px;
      }
      .item-count{
         font-weight: 700;
      }
    }
  }
  